import express from "express";
import Routes from "./routes";
/**
 * @description Express Class
 * @export
 * @class App
 */
class App {
    /**
     *Creates an instance of App.
     * @param {Application} app
     * @memberof App
     */
    constructor() {
        this.app = express();
        this.addMiddleWares();
    }
    /**
     * @description
     * @memberof App
     */
    addMiddleWares() {
        this.app = Routes(this.app);
    }
}
export default () => { const { app } = new App(); return app; };
